import React, { Component } from 'react'
import { HomeContainer, SubTitle, TimerLabel, Display, LampsContainer, BPMButtonsContainer, SelectBox } from './style'

import Button from '../../components/Button'
import StopPlayButton from '../../components/StopPlayButton'
import Beat from '../../components/Beat'
import Lamp from '../../components/Lamp'
import Header from '../../components/Header'
import Select from '../../components/Select'

import beat1 from '../../assets/beats/004.mp3'
import beat2 from '../../assets/beats/003.mp3'
import beat3 from '../../assets/beats/005.mp3'

import {
    initialState,
    handleBeats,
    handleChange,
    handleClicks,
    playClicks, play, startStop, save
} from './component'

export default class Home extends Component {
    constructor(props) {
        super(props)

        this.state = initialState
        
        this.handleChange = this.handleChange.bind(this)
        this.startStop = this.startStop.bind(this)
        this.play = this.play.bind(this)
        this.playClicks = this.playClicks.bind(this)
        this.save = this.save.bind(this)
        this.handleBeats = this.handleBeats.bind(this)
        this.handleClicks = this.handleClicks.bind(this)
    }
    
    b1 = null
    b2 = null
    b3 = null

    componentDidMount() {
        this.b1 = document.getElementById('beat-1')
        this.b2 = document.getElementById('beat-2')
        this.b3 = document.getElementById('beat-3')

        const state = JSON.parse(window.localStorage.getItem('default')) || initialState

        this.setState(state)
    }

    handleChange = handleChange
    handleBeats = handleBeats
    handleClicks = handleClicks
    play = play
    playClicks = playClicks
    startStop = startStop
    save = save

    render() {
        const { bpm, beats, clicks, beatCounter, status } = this.state
        function makeLamps() {
            const arr = []
        
            for(let i = 1; i <= beats; i++) {
                if(i === 1)
                    arr.push(<Lamp color="#e20000" light={ beatCounter === i } key={ i ** Math.random() * 1 * 3 } />)
                else
                    arr.push( <Lamp color="#fd831a" light={ beatCounter === i } key={ i ** Math.random() * 1 * 3 } />)
            }
            
            return arr    
        }

        return (
            <HomeContainer>
                <Header title="Me-tronome" save={ this.save } />
    
                <div className="row">
                    <LampsContainer className="col">
                        { makeLamps() }
                    </LampsContainer>
                </div>
    
                <div className="row">
                    <div className="col">
                        <SubTitle>BPM</SubTitle>
                    </div>
                </div>
    
                <Display className="row">
                    <BPMButtonsContainer className="col-4 column-d">
                        <Button action={ () => this.handleChange(bpm - 1) }>-1</Button>
                        <Button action={ () => this.handleChange(bpm - 5) }>-5</Button>
                    </BPMButtonsContainer>
    
                    <div className="col-2">
                        <TimerLabel>{ bpm }</TimerLabel>
                    </div>
    
                    <BPMButtonsContainer className="col-4 column-d">
                        <Button action={ () => this.handleChange(bpm + 1) }>+1</Button>
                        <Button action={ () => this.handleChange(bpm + 5) }>+5</Button>
                    </BPMButtonsContainer>
                </Display>

                <div className="row">
                    <SelectBox className="col-2">
                        Beats per bar
                        <Select size={ 16 } selected={ beats } action={ this.handleBeats } />
                    </SelectBox>

                    <SelectBox className="col-2">
                        Clicks per beat
                        <Select size={ 16 } selected={ clicks } action={ this.handleClicks } />
                    </SelectBox>
                </div>
    
                <div className="row" style={{ height: '50%' }}>
                    <div className="col">
                        <StopPlayButton
                            icon={ status ? "stop" : "play" }
                            action={ this.startStop }
                        />
                    </div>
    
                    <Beat id="beat-1" audio={ beat1 } />
                    <Beat id="beat-2" audio={ beat2 } />
                    <Beat id="beat-3" audio={ beat3 } />
                </div>
            </HomeContainer>
        )
    }
}

