export const initialState = {
    name: 'default',
    status: false,
	bpm: 60,
	beats: 4,
    clicks: 1,
    beatCounter: 0,
    clickCounter: 1,
	speed: '100%',
	timer: {
        status: false,
        method: 'time',
        tempo: '1m30s',
    },
    trainer: {
        status: false,
        type: 'increase',
        method: 'bars',
        tempo: '16'
    }
}

export function handleChange(bpm) {
    if(bpm < 30)
        bpm = 30
    else if (bpm > 300)
        bpm = 300
    
    this.setState({ bpm })
}

export function handleBeats(beats) {
    this.setState({ beats })
}

export function handleClicks(clicks) {
    this.setState({ clicks })
}

export function playClicks(interval) {
    const d = Date.now()
    const { clicks, clickCounter } = this.state

    if(clicks === 1 || clickCounter === clicks) {
        this.setState({ clickCounter: 1 })
        return
    }

    let c = clickCounter + 1
    this.b3.currentTime = 0
    this.b3.play()

    this.setState({ clickCounter: c })
    return setTimeout(() => this.playClicks(interval), interval - (Date.now() - d))
}

export function play() {
    const d = Date.now()
    const { bpm, beats, clicks, beatCounter, status } = this.state

    if(!status) return

    let b = beatCounter

    if(b >= beats || b === 0)
        b = 1
    else
        b++

    if(b === 1) {
        this.b1.currentTime = 0
        this.b1.play()
    } else {
        this.b2.currentTime = 0
        this.b2.play()
    }

    // clicks
    if(clicks > 1) {
        const clickInterval = (60000 / bpm) / clicks
        setTimeout(() => this.playClicks(clickInterval), clickInterval - (Date.now() - d))
    }

    this.setState({ beatCounter: b })
    return setTimeout(() => this.play(status), (60000 / bpm) - (Date.now() - d))
}

export function startStop() {
    let { status, beatCounter } = this.state

    if(!status) {
        status = true
    } else {
        status = false
        beatCounter = 0
    }

    this.setState({ status, beatCounter }, this.play)
}

export function save() {
    let { beatCounter, clickCounter, status } = this.state

    // evitar bugs ao salvar
    beatCounter = 0
    clickCounter = 1
    status = false

    const save = { ...this.state, beatCounter, clickCounter, status }
    window.localStorage.setItem('default', JSON.stringify(save))
    alert('Saved!')
}