import React, { useState } from 'react'
import { Select, Option, Mask } from './style'

export default function({ size, selected, action }) {
    const arr = []
    for(let i = 1; i <= size; i++)
        arr.push(i)
    
    function handleOptionClick(value) {
        setOpen(false)
        action(value)
    }

    function makeOptions() {
        return arr.map((value, i) => (
            <Option
                key={ value ** Math.random() * i }
                onClick={ () => handleOptionClick(value) }
            >
                { value }
            </Option>
        ))
    }

    const [ open, setOpen ] = useState(false)

    return (
        <Select open={ open } >
            {
                !open
                    ? <Mask onClick={ () => setOpen(true) } >{ selected }</Mask>
                    : makeOptions() }
        </Select>
    )
}